/* Using border-box across */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #eee;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 1rem 0;
  color: #222;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.3rem;
}

/* Default font-size to scale other elements from for tablet/mobile devices */
@media (max-width: 960px) {
  html {
    font-size: 14px;
  }
}

.search-wrapper {
  position: relative;
  min-width: 120px;
  width: 25%;
}

.search-wrapper::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 1.8rem;
  background: url(/feed-example/public/static/media/search_icon.4511fe6e.svg) no-repeat center;
  background-size: 1.5rem;
}

.search-wrapper .search-bar {
  width: 100%;
  padding: 0.5rem 2rem 0.5rem 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  color: #444;
  box-shadow: 0 2px 4px #f3f3f3;
}
.sort-wrapper {
  display: flex;
  align-items: center;
  min-width: 180px;
  width: 25%;
}

.sort-wrapper .label {
  padding-right: 0.5rem;
  width: 90px;
  text-align: right;
}

.sort-wrapper .select-box select {
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  color: #444;
  box-shadow: 0 2px 4px #f3f3f3;
  width: 100%;
}
.card-wrapper {
  width: 33%;
  padding: 0.75rem;
  display: flex;
}

.card {
  background: #fbfbfb;
  box-shadow: 0 2px 4px #f3f3f3;
  padding: 1rem;
  border: 1px solid #f3f3f3;
  border-radius: 4px;
}

.card h3 {
  padding: 0 0 1.5rem;
}

.card .card-img {
  width: 100%;
}

.card small {
  color: #666;
  font-size: 0.8rem;
}

@media (min-width: 601px) and (max-width: 960px) {
  .card-wrapper {
    width: 50%;
  }
}

@media (max-width: 600px) {
  .card-wrapper {
    width: 100%;
  }
}
.scroll-overlay {
  position: relative;
  margin: 1.75rem;
}

/* 'scroll-overlay' mimics a fixed semi-transparent vertical bar
    to the right-side of the table to indicate the user to scroll right
*/
.scroll-overlay::after {
  content: '';
  display: none;
  height: 100%;
  width: 1rem;
  background: linear-gradient(to right,  rgba(255,255,255,0) 0%,rgba(230,230,230,1) 100%);
  position: absolute;
  top: 0;
  right: 0;
}

.table-wrapper {
  max-width: 100%;
  overflow-x: auto;
}

.feed-table {
  border-width: 1px 1px 0 0;
  border-style: solid;
  border-color: #eee;
  border-collapse: collapse;
}

.feed-table thead {
  background-color: #fbfbfb;
}

.feed-table td, .feed-table th {
  padding: 0.5rem 1rem;
  border-width: 0 0 1px 1px;
  border-style: solid;
  border-color: #eee;
}

.feed-table .card-name {
  width: 20%;
}

.feed-table .card-img-wrap {
  width: 20%;
}

.feed-table .card-desc {
  width: 40%;
}

.feed-table .card-date {
  width: 20%;
}

.feed-table .cell-img {
  width: 100%;
}

/* Custom width of table, individual columns for Tablet and mobile devices to introduce scrolling */
@media (min-width: 601px) and (max-width: 960px) {
  .scroll-overlay::after {
    display: block;
  }

  .feed-table {
    width: 130%;
  }

  .feed-table .card-name {
    width: 30%;
  }

  .feed-table .card-img-wrap {
    width: 20%;
  }

  .feed-table .card-desc {
    width: 50%;
  }

  .feed-table .card-date {
    width: 30%;
  }
}

@media (max-width: 600px) {
  .scroll-overlay::after {
    display: block;
  }

  .feed-table {
    width: 200%;
  }
  
  .feed-table .card-name {
    width: 30%;
  }

  .feed-table .card-img-wrap {
    width: 25%;
  }

  .feed-table .card-desc {
    width: 120%;
  }

  .feed-table .card-date {
    width: 25%;
  }
}
.feed-container h1 {
  padding-left: 1.75rem;
  padding-right: 1.75rem;
}

.feed-container h2 {
  margin-top: 2rem;
  padding: 0 1.75rem;
}

.feed-container .filter-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1.75rem;
}

.feed-container .cards-container {
  display: flex;
  flex-wrap: wrap;
  padding: 0 1rem;
}
.app-container {
  max-width: 1280px;
  background: #fff;
  margin: 0 15px;
  min-height: 100vh;
  padding-bottom: 2rem;
}

@media (min-width: 1281px) {
  .app-container {
    margin: 0 auto;
  }
}
